.login-container {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: #ffe4b5; /* Warm peach background color */
  font-family: 'Arial', sans-serif;
}

h2 {
  color: #8b4513; /* Warm brown color */
  margin-bottom: 20px;
}

.login-container input {
  width: 250px;
  padding: 10px;
  border: none;
  border-bottom: 2px solid #8b4513;
  margin-bottom: 15px;
  background-color: transparent;
  outline: none;
  transition: border-color 0.3s;
}

.login-container input:focus {
  border-bottom-color: #daa520; /* Warm gold color when focused */
}

.login-container button {
  padding: 10px 20px;
  border: none;
  border-radius: 20px;
  background-color: #daa520; /* Warm gold color */
  color: white;
  cursor: pointer;
  transition: background-color 0.3s, transform 0.3s;
}

.login-container button:hover {
  background-color: #b8860b; /* A slightly darker gold color when hovered */
  transform: scale(1.05); /* Slightly enlarges the button */
}
